package com.minutiae.game.event.types

import model.Category
import com.minutiae.weight.MutableWeighted
import com.minutiae.weight.getRandomItem
import kotlin.random.Random

class SelectCategoryEvent(
    val categories: List<MutableWeighted<Category>>
) : Event() {
    fun getCategory(random: Random) = categories.getRandomItem(random) { it.weight }.item
}