package com.minutiae.game.modifier

import androidx.compose.runtime.Composable
import com.minutiae.game.SimpleText
import com.minutiae.game.event.types.*
import com.minutiae.game.gameInstance
import kotlin.math.max
import model.Category
import kotlin.reflect.KClass

object GameModifiers : Iterable<GameModifier> {
    private val items = mutableListOf<GameModifier>()

    val addBlue = modifier("Blue", 2, 5f) {
        ScoreQuestionEvent::class {
            it.time += 5
        }
        describe { SimpleText("Add 5 to blue when answers are scored.") }
    }

    val addRed = modifier("Red", 2, 5f) {
        ScoreQuestionEvent::class {
            it.guesses += 3
        }
        describe { SimpleText("Add 3 to red when answers are scored.") }
    }

    val addGreen = modifier("Green", 2, 5f) {
        ScoreQuestionEvent::class {
            it.difficulty += 2
        }

        describe { SimpleText("Add 2 to green when answers are scored.") }
    }

    val doubleScore = modifier("Double Score", 4, 3f) {
        ScoreQuestionEvent::class {
            it.additional *= 2
        }
        describe { SimpleText("Answers are worth 2x as many points.") }
    }

    val tripleScore = modifier("Triple Score", 8, 1f) {
        ScoreQuestionEvent::class {
            it.additional *= 3
            val newUses = get<Int>("uses")!! - 1
            if (newUses == 0) {
                gameInstance?.removeModifier(this)
            } else {
                this["uses"] = newUses
            }
        }

        data {
            mapOf("uses" to 10)
        }

        describe {
            SimpleText("Answers are worth 3x as many points.")
            SimpleText("Disappears after ${it["uses"]} uses.")
        }
    }

    val stacked = modifier("Stacked", 6, 3f) {
        ScoreQuestionEvent::class {
            val boost = get<Int>("points")!!
            it.time += boost
            this["points"] = boost + it.question.question.difficulty.multiplier
        }

        SkipQuestionEvent::class {
            this["points"] = max(get<Int>("points")!! - 2, 0)
        }

        data { mapOf("points" to 0) }

        describe {
            SimpleText("Gives ${it["points"]} additional blue point(s).")
            SimpleText("Skipping a question decreases this by 2.")
            SimpleText("Answering a question increases this by its difficulty value.")
        }
    }

    val forgiveness = modifier("Forgiveness", 5, 5f) {
        RunInfoEvent::class {
            it.questions += 2
            it.skips += 1
        }

        describe {
            SimpleText("Grants 2 additional questions")
            SimpleText("Grants 1 additional skip")
        }
    }

    val copycat = modifier("Copycat", 6, 2f) {
        Event::class { event ->
            val modifiers = gameInstance!!.modifiers
            val index = modifiers.indexOf(this)
            modifiers.getOrNull(index + 1)?.takeIf { it.modifier != copydog }?.onEvent(event)
        }

        describe {
            SimpleText("Copy the ability of the modifier to the right")
        }
    }

    val copydog: GameModifier = modifier("Copydog", 6, 2f) {
        Event::class { event ->
            val modifiers = gameInstance!!.modifiers
            val index = modifiers.indexOf(this)
            modifiers.getOrNull(index - 1)?.takeIf { it.modifier != copycat }?.onEvent(event)
        }

        describe {
            SimpleText("Copy the ability of the modifier to the left")
        }
    }

    init {
        modifier("Commonality", 4, 3f) {
            SelectCategoryEvent::class { event ->
                val category = get<Category>("category")!!
                event.categories.find { it.item === category }?.let { it.weight += 10f }
            }

            data { mapOf("category" to Category.entries.random()) }
            name { "Commonality [${(it["category"] as Category).displayName}]" }
            describe { SimpleText("${(it["category"] as Category).displayName} questions are more common.") }
        }

        modifier("Boost", 4, 3f) {
            ScoreQuestionEvent::class {
                if (it.question.question.category === get<Category>("category")!!) it.additional *= 2
            }

            data { mapOf("category" to Category.entries.random()) }
            name { "Boost [${(it["category"] as Category).displayName}]" }
            describe { SimpleText("${(it["category"] as Category).displayName} questions are worth 2x more.") }
        }
    }

    private fun modifier(
        name: String,
        value: Int,
        weight: Float,
        block: GameModifierBuilder.() -> Unit
    ): GameModifier {
        return GameModifierBuilder(name, value, weight).also(block).getModifier().also(items::add)
    }

    override fun iterator() = items.iterator()
}

class GameModifierBuilder(
    name: String,
    private val value: Int,
    private val weight: Float,
) {
    private val events = mutableMapOf<KClass<out Event>, MutableList<GameModifierEventHandler<Event>>>()

    private var name: (Map<String, Any?>) -> String = { name }
    private var description: @Composable (Map<String, Any?>) -> Unit = {}
    private var data: () -> Map<String, Any?> = { emptyMap() }

    fun name(block: (Map<String, Any?>) -> String) {
        name = block
    }

    fun describe(block: @Composable (Map<String, Any?>) -> Unit) {
        description = block
    }

    fun data(block: () -> Map<String, Any?>) {
        data = block
    }

    fun getModifier() = GameModifier(name, value, weight, events, data, description)

    operator fun <T : Event> set(eventClass: KClass<out T>, value: GameModifierEventHandler<T>) {
        events.getOrPut(eventClass, ::mutableListOf).add(value as GameModifierEventHandler<Event>)
    }

    operator fun <T : Event> KClass<out T>.invoke(value: GameModifierEventHandler<T>) {
        set(this, value)
    }
}
