package com.minutiae.game

import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import com.minutiae.game.themes.Shades
import com.minutiae.game.themes.Theme
import com.minutiae.game.themes.Themes
import org.jetbrains.compose.web.css.*

private var themeIndex = 0
var Styles by mutableStateOf(ActiveStyle(Themes.themes[themeIndex])); private set

fun cycleTheme() {
    themeIndex = (themeIndex + 1) % Themes.themes.size
    Styles = ActiveStyle(Themes.themes[themeIndex])
}

class ActiveStyle(private val theme: Theme) : StyleSheet() {
    init {
        (type("*")) {
            fontFamily("Arial")
            cursor("default")
            color(theme.text.t950)
            boxSizing("border-box")
        }
    }

    val pageContainer by style {
        position(Position.Absolute)
        backgroundColor(theme.background.t50)
        left(0.px)
        top(0.px)
        width(100.0.percent)
        minHeight(100.0.percent)
        display(DisplayStyle.Flex)
        padding(10.px)
    }

    val standardButton by style {
        backgroundColor(theme.accent.t500)
        border(4.px, LineStyle.Solid, theme.accent.t700)
        borderRadius(10.px)

        padding(10.px)

        fontSize(20.px)
        fontWeight(600)

        property("width", "fit-content")
        property("height", "fit-content")

        self + hover style {
            backgroundColor(theme.accent.t700)
            border(4.px, LineStyle.Solid, theme.accent.t900)
        }
        self + disabled style {
            backgroundColor(rgb(127, 127, 127))
            border(4.px, LineStyle.Solid, rgb(191, 191, 191))
        }
    }

    val mainMenu by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
    }



    val wholeScreen by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)
        flexGrow(1)
        gap(10.px)
    }

    val playScreen by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        gap(10.px)
        flexGrow(1)
    }

    val loadingContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)

        backgroundColor(theme.primary.t500)
        border(4.px, LineStyle.Solid, theme.primary.t700)
        borderRadius(10.px)

        padding(10.px)
        margin(10.px)
    }

    val loadingLabel by style {
        textAlign("center")
        fontSize(50.px)
        fontWeight(900)
    }

    val gameOverContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)
    }

    val gameSummary by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)

        backgroundColor(theme.primary.t500)
        border(4.px, LineStyle.Solid, theme.primary.t700)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")

        padding(20.px)

    }

    val roundOverScreen by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)
    }

    val roundOverContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)

        backgroundColor(theme.primary.t500)
        border(4.px, LineStyle.Solid, theme.primary.t700)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")

        padding(10.px)
        gap(10.px)
    }

    val roundOverSummary by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)

        textAlign("center")

        backgroundColor(theme.secondary.t500)
        border(4.px, LineStyle.Solid, theme.secondary.t700)
        borderRadius(10.px)

        padding(10.px)
    }

    private fun StyleScope.componentFromShades(shades: Shades) {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)

        textAlign("center")

        backgroundColor(shades.t500)
        border(4.px, LineStyle.Solid, shades.t700)
        borderRadius(10.px)

        padding(10.px)
    }

    val secondaryComponent by style { componentFromShades(theme.secondary) }

    val simpleText by style {
        display(DisplayStyle.Flex)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)
    }

    val shopScreen by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)
    }

    val shopContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)

        backgroundColor(theme.primary.t500)
        border(4.px, LineStyle.Solid, theme.primary.t700)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")

        padding(10.px)
        gap(10.px)
    }

    val shopSummary by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)

        textAlign("center")

        backgroundColor(theme.secondary.t500)
        border(4.px, LineStyle.Solid, theme.secondary.t700)
        borderRadius(10.px)

        padding(10.px)
    }

    val questionsPlayContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)
        gap(10.px)
    }

    val questionsContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)
        justifyContent(JustifyContent.Center)
        width(100.0.percent)
        gap(10.px)
    }

    val question by style {
        display(DisplayStyle.Flex)
        alignItems(AlignItems.Center)

        backgroundColor(theme.primary.t500)
        border(4.px, LineStyle.Solid, theme.primary.t700)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")

        padding(10.px)
        gap(10.px)


    }

    val questionHidden by style {
        flexDirection(FlexDirection.Column)
        width(50.0.percent)
    }
    val questionRevealed by style {
        flexDirection(FlexDirection.Row)
    }

    val questionCategoryAndText by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)
        gap(10.px)
    }

    val answersContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)

        gap(10.px)
    }

    val answer by style {
        textAlign("center")

        width(100.0.percent)

        padding(10.px)

        backgroundColor(theme.accent.t500)
        border(4.px, LineStyle.Solid, theme.accent.t700)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")


        (self + hover) {
            backgroundColor(theme.accent.t700)
            border(4.px, LineStyle.Solid, theme.accent.t900)
        }
    }

    val wrongGuess by style {
        textDecoration("line-through")
        backgroundColor(rgb(127, 127, 127))
        border(4.px, LineStyle.Solid, rgb(63, 63, 63))
    }

    val scoreContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)

        backgroundColor(theme.secondary.t300)
        border(4.px, LineStyle.Solid, theme.secondary.t500)
        borderRadius(10.px)

        padding(5.px)

    }

    val scoreValue by style {
        textAlign("center")
        fontSize(34.px)
        fontWeight(900)
    }

    val scoreLabel by style {
        textAlign("center")
        fontSize(20.px)
        fontWeight(900)
    }

    val scoreValues by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)
        justifyContent(JustifyContent.Center)
        alignItems(AlignItems.Center)
    }

    val scoreComponent by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)

        borderRadius(10.px)
        padding(5.px)
    }

    val scoreComponentValue by style {
        textAlign("center")
        fontSize(24.px)
        fontWeight(900)
    }

    val scoreComponentLabel by style {
        textAlign("center")
        fontSize(16.px)
        fontWeight(600)
    }

    val blueScore by style {
        backgroundColor(rgb(127, 127, 255))
        border(4.px, LineStyle.Solid, rgb(0, 0, 255))
    }

    val redScore by style {
        backgroundColor(rgb(255, 127, 127))
        border(4.px, LineStyle.Solid, rgb(255, 0, 0))
    }

    val greenScore by style {
        backgroundColor(rgb(127, 255, 127))
        border(4.px, LineStyle.Solid, rgb(0, 255, 0))
    }

    val modifierContainer by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)
        justifyContent(JustifyContent.SpaceEvenly)
        minHeight(100.px)
        width(100.0.percent)
        gap(10.px)
    }

    val modifier by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        alignItems(AlignItems.Center)

        backgroundColor(theme.accent.t300)
        border(4.px, LineStyle.Solid, theme.accent.t500)
        borderRadius(10.px)

        property("box-shadow", "4px 4px 8px")

        padding(10.px)
        textAlign("center")
    }

    val modifierName by style {
//        fontSize(24.px)
        fontWeight(900)
    }

    val modifierButtons by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Row)
        justifyContent(JustifyContent.Center)
        width(100.0.percent)
        padding(0.px)
        margin(0.px)
    }

    val modifierButton by style {
//        fontSize(20.px)
        padding(0.px)
    }

    val sidebar by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)
        justifyContent(JustifyContent.SpaceBetween)


    }

    val runInfo by style {
        display(DisplayStyle.Grid)
        gridTemplateColumns("1fr 1fr")
    }

    val runInfoItem by style {
        display(DisplayStyle.Flex)
        flexDirection(FlexDirection.Column)

        margin(5.px)

        backgroundColor(theme.primary.t300)
        border(4.px, LineStyle.Solid, theme.primary.t500)
        borderRadius(10.px)
    }

    val runInfoItemValue by style {
        textAlign("center")
        fontSize(24.px)
        fontWeight(900)
    }

    val runInfoItemLabel by style {
        textAlign("center")
        fontSize(16.px)
        fontWeight(600)
    }
}