package com.minutiae.game

import androidx.compose.runtime.*
import com.minutiae.game.modifier.GameModifiers
import com.minutiae.weight.takeRandomItems
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text
import kotlin.random.Random

class ShopInstance(private val random: Random) : PlayScreen {
    var modifiers by mutableStateOf(
        GameModifiers.toMutableList().takeRandomItems(random, 2) { it.weight }.map {
            it.getInstance()
        }
    )

    @Composable
    override fun render(instance: GameInstance) {
        Div({ classes(Styles.shopScreen) }) {
            Div({ classes(Styles.shopContainer) }) {
                Div({ classes(Styles.shopSummary) }) {
                    SimpleText("Buy modifiers to help your run!")
                }

                Div({ classes(Styles.modifierContainer) }) {
                    modifiers.forEach { modifier ->
                        modifier.renderInShop(instance, this@ShopInstance)
                    }
                }

                StandardButton({
                    onClick { instance.startRound() }
                }) {
                    Text("Next Round")
                }
            }
        }
    }
}