package com.minutiae.game

import androidx.compose.runtime.Composable
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text

class RoundOver(private val round: RoundInstance) : PlayScreen {
    @Composable
    override fun render(instance: GameInstance) {
        Div({ classes(Styles.roundOverScreen) }) {
            Div({ classes(Styles.roundOverContainer) }) {
                val questionsRemaining = round.questionsRemaining
                val skipsRemaining = round.skipsRemaining
                val total = questionsRemaining + skipsRemaining / 2

                Div({ classes(Styles.roundOverSummary) }) {
                    SimpleText("You scored ${round.totalScore}!")
                    SimpleText("$questionsRemaining questions remaining = $$questionsRemaining")
                    SimpleText("$skipsRemaining skips remaining = $${skipsRemaining / 2}")
                    SimpleText("Total = $$total")
                }

                StandardButton({
                    onClick {
                        instance.money += total
                        instance.openShop()
                    }
                }) { Text("Continue") }
            }
        }
    }
}