package com.minutiae.game.modifier

import androidx.compose.runtime.Composable
import androidx.compose.runtime.mutableStateOf
import com.minutiae.game.event.types.Event
import com.minutiae.game.event.forEachMatching
import kotlin.reflect.KClass

typealias GameModifierEventHandler<T> = GameModifierInstance.(T) -> Unit

class GameModifier(
    private val name: (Map<String, Any?>) -> String,
    private val value: Int,
    val weight: Float,
    private val events: Map<KClass<out Event>, List<GameModifierEventHandler<Event>>>,
    private val data: () -> Map<String, Any?>,
    private val description: @Composable (Map<String, Any?>) -> Unit,
) {
    fun onEvent(instance: GameModifierInstance, event: Event) {
        events.forEachMatching(event) { handlers ->
            handlers.forEach { it(instance, event) }
        }
    }

    fun getName(data: Map<String, Any?>) = name(data)
    @Composable
    fun describe(data: Map<String, Any?>) = description(data)

    fun getInstance() = GameModifierInstance(this, value, mutableStateOf(data()))
}