package com.minutiae.game.event

import com.minutiae.game.event.types.Event
import com.minutiae.game.gameInstance

object GameModifierEventHandler {
    fun listeners(bus: EventBus) {
        bus[Event::class] = ::onEvent
    }

    private fun onEvent(event: Event) {
        gameInstance?.modifiers?.forEach {
            it.onEvent(event)
        }
    }
}