package com.minutiae.game

import androidx.compose.runtime.Composable
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text

class GameOver : PlayScreen {
    @Composable
    override fun render(instance: GameInstance) {
        Div({ classes(Styles.gameOverContainer) }) {
            Div({ classes(Styles.gameSummary) }) {
                StyledText("Game Over!") { classes(Styles.simpleText) }

                StandardButton({
                    onClick {
                        newGame()
                    }
                }) { Text("New Game") }

                StandardButton({
                    onClick {
                        clearGame()
                    }
                }) { Text("Main Menu") }
            }
        }
    }
}