package com.minutiae.game

import androidx.compose.runtime.*
import com.minutiae.game.event.EventBus
import com.minutiae.game.event.types.RoundWinEvent
import com.minutiae.game.event.types.SelectCategoryEvent
import com.minutiae.game.event.types.SkipQuestionEvent
import model.Difficulty
import model.Category
import com.minutiae.weight.MutableWeighted
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import org.jetbrains.compose.web.attributes.disabled
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text
import kotlin.random.Random

class RoundInstance(
    val requiredScore: Int,
    val questions: Int,
    val skips: Int,
    private val random: Random
) : PlayScreen {
    private var currentQuestions by mutableStateOf(emptyList<QuestionInstance>())

    var totalScore by mutableStateOf(0); private set

    var skipsRemaining by mutableStateOf(skips); private set
    var questionsRemaining by mutableStateOf(questions)

    fun onCorrectAnswer(score: Int, instance: GameInstance) {
        totalScore += score
        cycleQuestions(instance)
    }

    fun onWrongAnswer(instance: GameInstance) {
        cycleQuestions(instance)
    }

    private fun onSkip() {
        EventBus.post(SkipQuestionEvent(currentQuestions.map { it.question }))
        skipsRemaining--
        updateQuestions()
    }

    private fun updateQuestions() {
        currentQuestions = emptyList()
        GlobalScope.launch {
            currentQuestions = listOf(
                getQuestionInstance(
                    Difficulty.easy,
                    getRandomCategory(random)
                ),
                getQuestionInstance(
                    listOf(Difficulty.medium, Difficulty.hard).random(random),
                    getRandomCategory(random)
                )
            )
        }
    }

    private suspend fun getQuestionInstance(difficulty: Difficulty, category: Category): QuestionInstance {
        return QuestionInstance(
            this,
            QuestionPool.getQuestion(difficulty, category)
        )
    }

    private fun cycleQuestions(instance: GameInstance) {
        if (totalScore >= requiredScore) {
            instance.roundsPlayed++
            EventBus.post(RoundWinEvent(totalScore, questionsRemaining, skipsRemaining))
            instance.currentScreen = RoundOver(this)
        } else if (questionsRemaining == 0) {
            instance.currentScreen = GameOver()
        } else {
            updateQuestions()
        }
    }

    private fun getRandomCategory(random: Random) = EventBus.post(
        SelectCategoryEvent(
            Category.entries.map {
                MutableWeighted(it, 5f)
            }
        )
    ).getCategory(random)

    @Composable
    override fun render(instance: GameInstance) {
        updateQuestions()

        Div({ classes(Styles.questionsPlayContainer) }) {
            Div({ classes(Styles.questionsContainer) }) {
                if (currentQuestions.isEmpty()) {
                    Loading()
                } else {
                    Questions(instance)
                }
            }

            StandardButton({
                if (skipsRemaining == 0) disabled()
                onClick { onSkip() }
            }) {
                Text("Skip!")
            }
        }
    }

    private val selectedQuestion get() = currentQuestions.find { it.started }

    @Composable
    override fun renderSideBar(instance: GameInstance): Boolean {
        Sidebar(
            questionsRemaining,
            skipsRemaining,
            instance.roundsPlayed,
            instance.money,
            selectedQuestion?.maxTime ?: 0,
            selectedQuestion?.guessesRemaining ?: 0,
            selectedQuestion?.difficultyMultiplier ?: 0,
            requiredScore,
            totalScore
        )
        return true
    }

    @Composable
    private fun Questions(instance: GameInstance) {
        selectedQuestion?.render(instance, false)
            ?: currentQuestions.forEach { it.render(instance, true) }
    }
}

@Composable
private fun Loading() {
    Div({ classes(Styles.loadingContainer) }) {
        StyledInlineText("Loading Questions . . .") { classes(Styles.loadingLabel) }
    }
}


