package com.minutiae.weight

import com.minutiae.game.sumOf
import kotlin.random.Random

data class Weighted<T>(val item: T, val weight: Float, val tail: Float = 1f)
data class MutableWeighted<T>(val item: T, var weight: Float, var tail: Float = 1f)

fun <T> MutableIterable<T>.takeRandomItems(
    random: Random,
    count: Int,
    multiplier: Float = 0f,
    getTail: (T) -> Float = { 1f },
    getWeight: (T) -> Float
): List<T> {
    return List(count) {
        val result = getRandomItem(random, multiplier, getTail, getWeight)
        removeAll { it === result }
        result
    }
}

fun <T> Iterable<T>.getRandomItems(
    random: Random,
    count: Int,
    multiplier: Float = 0f,
    getTail: (T) -> Float = { 1f },
    getWeight: (T) -> Float
): List<T> {
    return List(count) { getRandomItem(random, multiplier, getTail, getWeight) }
}



fun <T> Iterable<T>.getRandomItemOrNull(
    random: Random,
    multiplier: Float = 0f,
    getTail: (T) -> Float = { 1f },
    getWeight: (T) -> Float
): T? {
    when (count()) {
        0 -> return null
        1 -> return first()
    }

    val totalWeight = sumOf(getWeight)
    if (totalWeight <= 0) return null

    val offset = totalWeight * multiplier
    val list = shuffled(random)
    val totalWeightWithTail = totalWeight + sumOf(getTail) * offset

    var threshold = random.nextFloat() * totalWeightWithTail
    for (item in list) {
        threshold -= (getWeight(item) + getTail(item) * offset)
        if (threshold <= 0) return item
    }

    return null
}

fun <T> Iterable<T>.calculateChances(
    multiplier: Float = 0f,
    getTail: (T) -> Float = { 1f },
    getWeight: (T) -> Float
): Map<T, Float> {
    val totalWeight = sumOf(getWeight)
    if (totalWeight <= 0) return emptyMap()

    val offset = totalWeight * multiplier
    val totalWeightWithTail = totalWeight + sumOf(getTail) * offset

    return associateWith { (getTail(it) * offset + getWeight(it)) / totalWeightWithTail }
}

fun <T> Iterable<T>.getRandomItem(
    random: Random,
    multiplier: Float = 0f,
    getTail: (T) -> Float = { 1f },
    getWeight: (T) -> Float
) = getRandomItemOrNull(
    random,
    multiplier,
    getTail,
    getWeight
) ?: throwException()


fun <T> Iterable<Weighted<T>>.getRandomItemOrNull(
    random: Random,
    multiplier: Float = 0f
) = getRandomItemOrNull(random, multiplier, { it.tail }) { it.weight }?.item

fun <T> Iterable<Weighted<T>>.getRandomItem(
    random: Random,
    multiplier: Float = 0f
) = getRandomItemOrNull(
    random,
    multiplier
) ?: throwException()

private fun throwException(): Nothing = throw NoSuchElementException("could not obtain weighted item")
