package com.minutiae.game

import androidx.compose.runtime.Composable
import org.jetbrains.compose.web.dom.Div



@Composable
fun Sidebar(
    questions: Int,
    skips: Int,
    rounds: Int,
    money: Int,
    blue: Int = 0,
    red: Int = 0,
    green: Int = 0,
    required: Int = 0,
    total: Int = 0
) {
    RequiredScore(required)
    TotalScore(total)
    ScoreValues(blue, red, green)
    RunInfo(questions, skips, rounds, money)
}

@Composable
private fun RequiredScore(required: Int) {
    Div({ classes(Styles.scoreContainer) }) {
        StyledInlineText("$required") { classes(Styles.scoreValue) }
        StyledInlineText("Required Score") { classes(Styles.scoreLabel) }
    }
}

@Composable
private fun TotalScore(total: Int) {
    Div({ classes(Styles.scoreContainer) }) {
        StyledInlineText("$total") { classes(Styles.scoreValue) }
        StyledInlineText("Total Score") { classes(Styles.scoreLabel) }
    }
}

@Composable
private fun ScoreValues(blue: Int, red: Int, green: Int) {
    Div({ classes(Styles.scoreValues) }) {
        ScoreValue(blue, Styles.blueScore, "Time")
        ScoreValue(red, Styles.redScore, "Guess")
        ScoreValue(green, Styles.greenScore, "Difficulty")
    }
}

@Composable
private fun ScoreValue(value: Int, style: String, label: String) {
    Div({ classes(Styles.scoreComponent, style) }) {
        StyledInlineText("$value") { classes(Styles.scoreComponentValue) }
        StyledInlineText(label) { classes(Styles.scoreComponentLabel) }
    }
}

@Composable
private fun RunInfo(questions: Int, skips: Int, rounds: Int, money: Int) {
    Div({ classes(Styles.runInfo) }) {
        RunInfoItem("$questions", "Questions")
        RunInfoItem("$skips", "Skips")
        RunInfoItem("$rounds", "Round")
        RunInfoItem("$$money", "Money")
    }
}
@Composable
private fun RunInfoItem(value: String, label: String) {
    Div({ classes(Styles.runInfoItem) }) {
        StyledInlineText(value) { classes(Styles.runInfoItemValue) }
        StyledInlineText(label) { classes(Styles.runInfoItemLabel) }
    }
}