package model

import io.ktor.resources.*
import kotlinx.serialization.Serializable
import kotlin.random.Random

@Serializable
data class Question(
    val category: Category,
    val id: String,
    val tags: List<String>,
    val difficulty: Difficulty,
    val regions: List<String>,
    val isNiche: Boolean,
    val question: QuestionText,
    val correctAnswer: String,
    val incorrectAnswers: List<String>,
    val type: String
) {
    val text get() = question.text

    fun getAnswers(random: Random): List<Answer> {
        return (incorrectAnswers.map { Answer(it, false) } + Answer(correctAnswer, true)).shuffled(random)
    }
}

@Serializable
data class Answer(
    val text: String,
    val isCorrect: Boolean
)

@Serializable
data class QuestionText(val text: String)

@Resource("/v2/questions")
class QuestionRequest(
    val limit: Int? = null,
    val categories: List<Category>? = null,
    val difficulties: List<Difficulty>? = null,
    val region: String? = null
)

enum class Category(val displayName: String) {
    music("Music"),
    sport_and_leisure("Sports & Leisure"),
    film_and_tv("Film & Television"),
    arts_and_literature("Arts & Literature"),
    history("History"),
    society_and_culture("Society & Culture"),
    science("Science"),
    geography("Geography"),
    food_and_drink("Food & Drink"),
    general_knowledge("General Knowledge")
}

enum class Difficulty(
    val displayName: String,
    val multiplier: Int,
    val weight: Float
) {
    easy("Easy", 1, 3f),
    medium("Medium", 2, 2f),
    hard("Hard", 3, 1f)
}
